import common from '../../../common/common.js'
import uniPopup from '../../../components/uni-popup.vue'

export default {
	data() {
		return {
			name:'aa',
			shopInfo:[],		//用户信息
			scrollLeft:0,
			tabIndex: 0,
			isClickChange:false,
			tabBars: [{
				name: '待配货',
				id: '0'
			}, {
				name: '已配货',
				id: '1'
			}],
			shenqingList: [],
			popType: 'middle',
			showPopupMiddle: false,
			orderid:0,
			goods_real_weight:0,
			index:'',
			subindex:'',
			order_no:'',
		}
	},
	onLoad() {
		var that = this
		var shopInfo = uni.getStorageSync('shopInfo');
		var utype =  uni.getStorageSync('utype');			//用户类型
		

		if(shopInfo == ''){
			uni.redirectTo({
				url: '/pages/userlogin/userlogin',
			});
			return false
		}
		
		that.getMerchantShengqingViewList();
	},
	onPullDownRefresh() {
		var that = this
		uni.showNavigationBarLoading();
		console.log('下拉刷新中...');
		
		that.getUserInfo();
		if(that.shopInfo.shop_id > 0){
			that.getMerchantShengqingViewList();
		}
		
		uni.hideNavigationBarLoading();
		uni.stopPullDownRefresh();
	},
	methods: {
		
		//
		jumpUrl(e){
			var that = this
			var url = e.currentTarget.dataset.url
			uni.redirectTo({
				url: url
			});
		},
		
		//同意
		updateShenqingStatus(e){
			var that = this
			var id = e.currentTarget.dataset.id
			var status = e.currentTarget.dataset.status
			var t_words = status == 1 ? '同意' : (status == 0 ? '解绑' : '拒绝');
			uni.showModal({
				title: '提示',
				content: '确定要'+t_words+'吗?',
				success: function (res) { 
				  if(res.confirm){
					  uni.request({
					  	url: common.sjApiUrl + 'merchant/updateShenqingStatus',
					  	method: 'GET',
					  	data: {
					  		status:status,
					  		id:id
					  	},
					  	success: res => {
					  		that.getMerchantShengqingViewList();
					  	},
					  	fail: () => {},
					  	complete: () => {}
					  });
				  }
				}
			})				
		},
		
		//获取商户订单列表
		getMerchantShengqingViewList(){
			var that = this
			var uid = uni.getStorageSync('userid');
			var shopInfo = uni.getStorageSync('shopInfo');
			var shop_id = shopInfo.shop_id
			console.log("shop_id",shop_id)
			uni.request({
				url: common.sjApiUrl + 'merchant/getMerchantShengqingViewList',
				method: 'GET',
				data: {
					shop_id:shop_id
				},
				success: res => {
					console.log("res",res)
					that.shenqingList = res.data.shenqing_list
				},
				fail: () => {},
				complete: () => {}
			});
		},		
		
		
		//更新获取用户信息
		getUserInfo(){
			//console.log('获取用户信息');
			var that = this
			var shopInfo = uni.getStorageSync('shopInfo');
			if(shopInfo == null || shopInfo == ''){
				uni.redirectTo({
					url: '/pages/userlogin/userlogin'
				});
				return false
			}
			
			console.log(shopInfo.mem_id)
			uni.request({
				url: common.sjApiUrl + 'merchant/getUserInfo',
				method: 'GET',
				data: {
					shop_id:shopInfo.shop_id
				},
				success: res => {
					var shop = res.data.userInfo
					console.log(shop)
					if(shop != ''){
						that.shopInfo = shop
						uni.setStorageSync('shopInfo',shop);
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
	},
	components: {
		uniPopup
	}
}
